#IV-view {
  display: flex;
  flex-direction: column;
  align-items: center; }
  #IV-view > * {
    margin-bottom: 18px; }
    #IV-view > *:last-child {
      margin-bottom: 0; }

.IV-video-container {
  position: relative; }

.IV-video-container video:nth-child(2) {
  position: absolute;
  top: 0;
  left: 0; }

.IV-button-container {
  display: flex;
  justify-content: center; }
  .IV-button-container > * {
    margin-right: 6px; }
    .IV-button-container > *:last-child {
      margin-right: 0; }
  .IV-button-container button {
    background-color: #005da8;
    color: white;
    border: none;
    border-radius: 3px;
    font-size: 1.5em; }

#IV-kickoff {
  font-size: 3em;
  position: relative;
  z-index: 100; }
